.login {
  display: grid;
  place-items: center;
  height: 100vh; /* max height it can take*/
  background-color: #000000;
}
.login > img {
  width: 50%;
  background-color: #000000;
}

.login > a {
  padding: 20px;
  border-radius: 99px;
  background-color: #1db954;
  font-weight: 800; /*to make text bold*/
  color: white;
  text-decoration: none; /* it will not look link now*/
}
